import { mapGetters } from 'vuex'

export default {
  layout: 'Admin',
  middleware: 'hasPermissions',
  fetchOnServer: false,
  meta: {
    permissions: ['USER_CAN_ACCESS_GALLERIES']
  },
  async fetch ({ store }) {
    await store.dispatch('gallery/fetchAll')
  },
  computed: {
    ...mapGetters('gallery', {
      galleries: 'list'
    })
  }
}
